import React, { useState } from 'react';
import { Card, CardHeader, CardContent, CardActions, Typography, Button, Checkbox, FormControlLabel, FormGroup, TextField, MenuItem } from '@mui/material';

const DatingFilter = () => {
  const [filters, setFilters] = useState({
    heightRange: [160, 180],
    weightRange: [50, 70],
    genders: [],
    ageRange: [25, 35],
    minIncome: 50000,
    education: '',
    ethnicities: [],
  });
  const [result, setResult] = useState(null);
  const [prScore, setPrScore] = useState(null);

  const [totalUsers, setTotalUsers] = useState(0);
  const [sumProbabilities, setSumProbabilities] = useState(0);
  const [sumSquaredProbabilities, setSumSquaredProbabilities] = useState(0);

  const handleChange = (name, value) => {
    setFilters(prev => ({ ...prev, [name]: value }));
  };

  const handleCheckboxChange = (name, value, checked) => {
    setFilters(prev => ({
      ...prev,
      [name]: checked 
        ? [...prev[name], value]
        : prev[name].filter(item => item !== value)
    }));
  };

  const calculateProbability = () => {
    let probability = 1;

    // 計算身高範圍概率
    const heightRange = filters.heightRange[1] - filters.heightRange[0];
    const heightProb = Math.min(1, heightRange / 60);
    probability *= heightProb;

    // 計算體重範圍概率
    const weightRange = filters.weightRange[1] - filters.weightRange[0];
    const weightProb = Math.min(1, weightRange / 50);
    probability *= weightProb;

    // 計算性別概率
    const genderProb = filters.genders.reduce((acc, gender) => {
      const genderProbabilities = {
        'male': 0.488,
        'female': 0.488,
        'transgender_male': 0.012,
        'transgender_female': 0.012
      };
      return acc + genderProbabilities[gender];
    }, 0);
    probability *= Math.min(1, genderProb);

    // 計算年齡範圍概率
    const ageRange = filters.ageRange[1] - filters.ageRange[0];
    const ageProb = Math.min(1, ageRange / 62);
    probability *= ageProb;

    // 計算收入概率
    const incomeProb = Math.max(0, 1 - (Math.log(filters.minIncome) - Math.log(30000)) / (Math.log(1000000) - Math.log(30000)));
    probability *= incomeProb;

    // 計算教育程度概率
    const educationProb = {
      'highschool': 0.6,
      'university': 0.25,
      'postgraduate': 0.05
    };
    const eduProb = filters.education ? educationProb[filters.education] : 1;
    probability *= eduProb;

    // 計算種族概率
    const ethnicityProb = filters.ethnicities.reduce((acc, ethnicity) => {
      const ethnicityProbabilities = {
        'east_asian': 0.21,
        'south_asian': 0.24,
        'southeast_asian': 0.08,
        'central_asian': 0.01,
        'white': 0.15,
        'black': 0.15,
        'hispanic': 0.09,
        'middle_eastern': 0.07
      };
      return acc + ethnicityProbabilities[ethnicity];
    }, 0);
    probability *= Math.min(1, ethnicityProb);

    // 更新統計數據
    const newTotalUsers = totalUsers + 1;
    const newSumProbabilities = sumProbabilities + probability;
    const newSumSquaredProbabilities = sumSquaredProbabilities + probability * probability;

    setTotalUsers(newTotalUsers);
    setSumProbabilities(newSumProbabilities);
    setSumSquaredProbabilities(newSumSquaredProbabilities);

    // 計算平均值和標準差
    const mean = newSumProbabilities / newTotalUsers;
    const variance = (newSumSquaredProbabilities / newTotalUsers) - (mean * mean);
    const stdDev = Math.sqrt(variance);

    // 計算PR值
    const prValue = ((probability - mean) / stdDev) * 100;
    setResult((probability * 100).toFixed(2));
    setPrScore(prValue.toFixed(2));
  };

  return (
    <Card style={{ maxWidth: 600, margin: 'auto', padding: '20px' }}>
      <CardHeader title="交往配對篩選器" />
      <CardContent>
        <div>
          <Typography gutterBottom>身高範圍 (cm)</Typography>
          <TextField
            label="最小身高"
            type="number"
            value={filters.heightRange[0]}
            onChange={(e) => handleChange('heightRange', [parseInt(e.target.value, 10), filters.heightRange[1]])}
            variant="outlined"
            margin="normal"
          />
          <TextField
            label="最大身高"
            type="number"
            value={filters.heightRange[1]}
            onChange={(e) => handleChange('heightRange', [filters.heightRange[0], parseInt(e.target.value, 10)])}
            variant="outlined"
            margin="normal"
          />
        </div>
        <div>
          <Typography gutterBottom>體重範圍 (kg)</Typography>
          <TextField
            label="最小體重"
            type="number"
            value={filters.weightRange[0]}
            onChange={(e) => handleChange('weightRange', [parseInt(e.target.value, 10), filters.weightRange[1]])}
            variant="outlined"
            margin="normal"
          />
          <TextField
            label="最大體重"
            type="number"
            value={filters.weightRange[1]}
            onChange={(e) => handleChange('weightRange', [filters.weightRange[0], parseInt(e.target.value, 10)])}
            variant="outlined"
            margin="normal"
          />
        </div>
        <div>
          <Typography gutterBottom>性別（可複選）:</Typography>
          <FormGroup>
            {['male', 'female', 'transgender_male', 'transgender_female'].map((gender) => (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={filters.genders.includes(gender)}
                    onChange={(e) => handleCheckboxChange('genders', gender, e.target.checked)}
                    name={gender}
                  />
                }
                label={gender === 'male' ? '男性' : gender === 'female' ? '女性' : gender === 'transgender_male' ? '跨性別男性' : '跨性別女性'}
                key={gender}
              />
            ))}
          </FormGroup>
        </div>
        <div>
          <Typography gutterBottom>年齡範圍</Typography>
          <TextField
            label="最小年齡"
            type="number"
            value={filters.ageRange[0]}
            onChange={(e) => handleChange('ageRange', [parseInt(e.target.value, 10), filters.ageRange[1]])}
            variant="outlined"
            margin="normal"
          />
          <TextField
            label="最大年齡"
            type="number"
            value={filters.ageRange[1]}
            onChange={(e) => handleChange('ageRange', [filters.ageRange[0], parseInt(e.target.value, 10)])}
            variant="outlined"
            margin="normal"
          />
        </div>
        <div>
          <Typography gutterBottom>最低年收入 (美元): ${filters.minIncome}</Typography>
          <TextField
            label="最低年收入"
            type="number"
            value={filters.minIncome}
            onChange={(e) => handleChange('minIncome', parseInt(e.target.value, 10))}
            variant="outlined"
            margin="normal"
          />
        </div>
        <div>
          <Typography gutterBottom>選擇教育程度:</Typography>
          <TextField
            select
            value={filters.education}
            onChange={(e) => handleChange('education', e.target.value)}
            fullWidth
            variant="outlined"
            margin="normal"
          >
            <MenuItem value="">選擇教育程度</MenuItem>
            <MenuItem value="highschool">高中及以上</MenuItem>
            <MenuItem value="university">大學及以上</MenuItem>
            <MenuItem value="postgraduate">研究所及以上</MenuItem>
          </TextField>
        </div>
        <div>
          <Typography gutterBottom>種族（可複選）:</Typography>
          <FormGroup>
            {['east_asian', 'south_asian', 'southeast_asian', 'central_asian', 'white', 'black', 'hispanic', 'middle_eastern'].map((ethnicity) => (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={filters.ethnicities.includes(ethnicity)}
                    onChange={(e) => handleCheckboxChange('ethnicities', ethnicity, e.target.checked)}
                    name={ethnicity}
                  />
                }
                label={
                  ethnicity === 'east_asian' ? '東亞人' :
                  ethnicity === 'south_asian' ? '南亞人' :
                  ethnicity === 'southeast_asian' ? '東南亞人' :
                  ethnicity === 'central_asian' ? '中亞人' :
                  ethnicity === 'white' ? '白人' :
                  ethnicity === 'black' ? '黑人' :
                  ethnicity === 'hispanic' ? '拉丁美洲裔' : '中東和北非裔'
                }
                key={ethnicity}
              />
            ))}
          </FormGroup>
        </div>
      </CardContent>
      <CardActions>
        <Button variant="contained" color="primary" onClick={calculateProbability}>計算機率</Button>
      </CardActions>
      {result !== null && (
        <CardContent>
          <Typography variant="h6">你的理想對象在人口中的比例約為: {result}%</Typography>
          <Typography variant="h6">你的擇偶條件PR值: {prScore}</Typography>
          <Typography>
            {prScore < 20
              ? "你的條件非常嚴格，找到完全符合條件的對象可能會很有挑戰性。考慮適當放寬一些要求可能會有幫助。"
              : prScore < 40
              ? "你的條件相對嚴格，可能需要多花些時間尋找理想對象。"
              : prScore < 60
              ? "你的條件適中，找到理想對象的難度適中。"
              : prScore < 80
              ? "你的條件相對寬鬆，有很多潛在的理想對象。"
              : "你的條件非常寬鬆，應該很容易找到合適的對象。"}
          </Typography>
        </CardContent>
      )}
    </Card>
  );
};
export default DatingFilter;